.sm_main_1 {
  width: 99%;
  max-height: 350px;
  padding: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  /* Force horizontal */
  gap: 10px;
  /* border: 1px solid blue; */
  overflow-x: auto;
  /* Enable horizontal scroll */
  overflow-y: hidden;
  /* Optional: hide vertical scroll */
  align-items: center;
  /* Center vertically */
  border-radius: 10px;
  scroll-behavior: smooth;
  /* Optional: smooth scroll */
}

.sm_main_1::-webkit-scrollbar {
  display: none;
}


